body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.RichEditor-root {
  background: #fff;
  border: 1px solid #ddd;
  font-family: "Georgia", serif;
  font-size: 14px;
  padding: 15px;
}

.RichEditor-editor {
  border-top: 1px solid #ddd;
  cursor: text;
  font-size: 16px;
  margin-top: 10px;
}

.RichEditor-editor .public-DraftEditorPlaceholder-root,
.RichEditor-editor .public-DraftEditor-content {
  margin: 0 -15px -15px;
  padding: 15px;
}

.RichEditor-editor .public-DraftEditor-content {
  min-height: 100px;
}

.RichEditor-hidePlaceholder .public-DraftEditorPlaceholder-root {
  display: none;
}

.RichEditor-editor .RichEditor-blockquote {
  border-left: 5px solid #eee;
  color: #666;
  font-family: "Hoefler Text", "Georgia", serif;
  font-style: italic;
  margin: 16px 0;
  padding: 10px 20px;
}

.RichEditor-editor .public-DraftStyleDefault-pre {
  background-color: rgba(0, 0, 0, 0.05);
  font-family: "Inconsolata", "Menlo", "Consolas", monospace;
  font-size: 16px;
  padding: 20px;
}

.RichEditor-controls {
  font-family: "Helvetica", sans-serif;
  font-size: 14px;
  margin-bottom: 5px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.RichEditor-styleButton {
  color: #999;
  cursor: pointer;
  margin-right: 16px;
  padding: 2px 0;
  display: inline-block;
}

.RichEditor-activeButton {
  color: #5890ff;
}

.tag-ratings-dropdown {
  list-style: none;
  display: flex;
  justify-content: space-between;
  margin: auto;
  padding: 0;
  width: 126px;
  font-size: 13px;
}

/* .dropdown-menu {
  min-width: 60px !important;
} */

#working-on-input {
  width: 300px;
}

.timer-grid {
  background-color: #ffffff;
  max-height: 100vh;
}

.fa-ellipsis-v {
  color: #7a7a7a;
}

.fa-ellipsis-v:hover {
  color: #383838;
}

.timer-task-container,
.timer-list-container,
.time-section,
.timer-nav_container,
.tag-menu-badge {
  display: flex;
}

.timer-task-container,
.timer-nav_container,
.tag-menu-badge {
  align-items: baseline;
}

.timer-task-container {
  height: 40px;
  line-height: 40px;
  background-color: #f3f3f3;
  border-bottom: 1px solid #d7c9aa70;
  padding: 0px 10px;
}

.time-container {
  width: 480px;
}

.timer-nav_container {
  height: 44px;
  border-bottom: 2px solid #6969696e;
}

.timer-time_container {
  padding: 0px;
}

.timer-nav-current-timer,
.total-time-seconds,
.timer-task-totaltime {
  width: 130px;
  font-size: 15px;
}

.timer-task-totaltime {
  font-weight: bold;
}

.timer-task-ul {
  /* width: 860px; */
  max-height: 320px;
  margin-bottom: 0px;
  overflow-y: scroll;
}

.timer-task-taskname {
  width: 300px;
  font-size: 16px;
  font-weight: bold;
}

.timer-list-container {
  height: 40px;
  line-height: 32px;
  padding-top: 2px;
  padding-bottom: 2px;
  border-bottom: solid 1px #efefef;
}

.timer-list-tag-menu {
  width: 35px;
}

.timer-list-start-end-time {
  display: flex;
  width: 380px;
}

.timer-task-badge,
.fas {
  cursor: pointer;
}

.fa-tag {
  color: #6c757d;
}

.fa-tag:hover {
  color: #343a40;
}

.fa-tags:hover {
  color: #005f16;
}

.timer-task-badge {
  min-width: 30px;
}

.timer-task-badge:hover {
  background-color: #0e6471;
}

.tags-active {
  color: #009823;
}

.tags-popover-body {
  max-height: 200px;
  overflow: scroll;
}

.dtp-picker {
  display: flex;
  align-items: baseline;
  justify-content: space-evenly;
}

body,
html {
  height: 100%;
}

.main-grid {
  background-color: #404040;
  height: 100vh;
  overflow: auto;
}

.bar:hover {
  fill: brown;
}

.navbar {
  background-color: #ffffff;
}

.navbar-brand {
  font-family: archivo;
  font-size: 30px;
}

.task-navigation-form {
  display: flex;
}

.task-center {
  height: 100vh;
  background-color: #ffffff;
  padding-left: 0px;
  padding-right: 0px;
}

.taskapp_container {
  background-color: #ffffff;
}

.tasks_container {
  padding: 20px;
}

.taskapp-header {
  margin: 0;
}

.tasks_list {
  max-height: 200px;
  overflow: scroll;
}

/************************ sidePanel CSS  ***************************/

.side-panel-active {
  background-color: #dadada;
}

.sidenav {
  height: 100vh;
  padding: 0px;
}

.side-panel {
  height: 100vh;
  overflow: scroll;
}

.task-item {
  height: 150px;
  border-bottom: 1px solid black;
}

.task-item-delete-button {
  float: right;
}

.task-item:hover {
  background-color: #dadada;
}

.task-item-content {
  position: relative;
  top: 5%;
  margin: auto;
  width: 90%;
  height: 90%;
}

.task-item-updated-date {
  position: absolute;
  bottom: 0px;
}

